//vars
$red: #d50000;
$white: #fff;
$black: #424242;
$gray: #bdbdbd;
$gray-secondary: #959595;
$gray-th: #d9d9d9;
$success: #43ab43;
$error: $red;

//fonts
@include font('Open Sans Regular', 'opensans-regular');
@include font('Open Sans Semibold', 'opensans-semibold');
@include font('Open Sans Bold', 'opensans-bold');

body {
	font: 400 18px/1.4 'Open Sans Regular', sans-serif;
	color: $gray-secondary;
	background-color: $red;
	text-align: center;
	@include smooth-font;
	@include md {
		font-size: 16px; }
	&.is-hidden {
		overflow: hidden; } }

a {
	text-decoration: none;
	color: $white; }

.btn,
.btn-default {
	font: 400 20px/1.4 'Open Sans Bold', sans-serif;
	text-align: center;
	border-radius: 30px;
	display: inline-block;
	background-color: $red;
	padding: 7px 59px;
	transition: background-color 0.25s;
	@include md {
		font-size: 16px;
		padding: 7px 30px; }
	&:hover {
		background-color: lighten($red, 5%); }
	&:active {
		transform: translateY(1px); } }

.btn {
	&_position {
		position: relative;
		z-index: 1; } }

.btn-default {
	color: $white; }

.btn-scroll {
	@include hide-text;
	padding: 10px;
	display: inline-block;
	&_position {
		position: relative;
		z-index: 1; }
	&__ico {
		width: 20px;
		padding: 20px 0 0;
		background: url(../img/arrow-bottom.svg) no-repeat center;
		background-size: contain;
		display: inline-block;
		vertical-align: middle;
		transition: transform 0.25s; }
	&:hover {
		.btn-scroll__ico {
			transform: translateY(5px); } } }

.btn-more {
	text-align: center;
	font: 400 16px/1.4 'Open Sans Bold', sans-serif;
	color: $red;
	text-decoration: none;
	display: inline-block;
	cursor: pointer;
	position: relative;
	&:after {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		right: 0;
		top: 100%;
		margin: -2px 0 0;
		height: 1px;
		background-color: $red;
		transition: opacity 0.25s, color 0.25s;
		opacity: 1; }
	&:hover {
		&:after {
			opacity: 0; } }
	&:active {
		top: 1px; } }

.btn-close {
	@include hide-text;
	cursor: pointer;
	background: none;
	position: absolute;
	top: 28px;
	right: 28px;
	padding: 10px;
	@include md {
		top: 10px;
		right: 10px; }
	.icon {
		transition fill 0.4s {}
		font-size: 32px;
		@include sm {
			font-size: 26px; } }
	@include hover {
		.icon {
			fill: lighten($gray, 10%); } } }

.title {
	font: 400 46px/1.2 'Open Sans Regular', sans-serif;
	color: $white;
	position: relative;
	z-index: 1;
	@include md {
		font-size: 32px; }
	@include sm {
		font-size: 26px; } }

.input {
	display: block;
	width: 100%;
	background-color: $white;
	border: 2px solid $gray;
	border-radius: 50px;
	color: $gray;
	font: 400 20px/1.2 'Open Sans Regular', sans-serif;
	padding: 16px 30px;
	transition: border-color 0.25s, color 0.25s;
	@include md {
		font-size: 16px;
		padding: 10px 20px; }
	@include placeholder {
		color: $gray; }
	&.error {
		border-color: $error;
		color: $error;
		@include placeholder {
			color: $error; } }
	&.valid {
		border-color: $success;
		color: $success;
		@include placeholder {
			color: $success; } } }
